  #app {
    height: 100vh;
  }
  .container-fluid {
    height: 94%;
  }
  .login {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #343a40;
    &__block {
      display: flex;
    }
    &__password {
      width: 12rem;
      margin-right: .5rem;
    }
  }
</style>
