<template>
  <div class="rolls">
    <div class="row">
      <div class="col-md-6">
        <div class="rolls__form">
          <form @submit.prevent="createRoll">
            <div class="form-group">
              <label for="orderName">Название:</label>
              <input type="text" class="form-control" id="orderName" v-model="form.name" required>
            </div>
            <div class="form-group">
              <label for="orderPrice">Себестоимость:</label>
              <input type="number" class="form-control" id="orderCost" v-model="form.cost" required>
            </div>
            <div class="form-group">
              <label for="orderPrice">Цена продажи:</label>
              <input type="number" class="form-control" id="orderPrice" v-model="form.price" required>
            </div>
            <button type="submit" class="btn btn-primary">Добавить</button>
          </form>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12" style="max-height: 100%">
        <div class="rolls__list">
          <v-table>
            <thead>
              <tr>
                <th scope="col">Название</th>
                <th scope="col">Себестоимость</th>
                <th scope="col">Цена продажи</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="{_id, name, products, cost, price} in rolls" :key=_id>
                <td>{{ name }}</td>
                <td>{{ cost }} руб.</td>
                <td>{{ price }} руб.</td>
                <td>
                  <button
                    type="button" 
                    class="btn btn-sm btn-danger"
                    @click="deleteRollItem(_id)"
                  >
                    Удалить
                  </button>
                </td>
              </tr>
            </tbody>
          </v-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import vTable from '@/components/v-table'

export default {
  name: 'Rolls',
  components: { vTable },
  data: () => ({
    form: {
      name: null,
      cost: null,
      price: null    
    }
  }),
  computed: {
    ...mapGetters(['rolls']),
  },
  methods: {
    ...mapActions(['createRollItem', 'deleteRoll']),
    createRoll() {
      const roll = {
        name: this.form.name,
        price: +this.form.price,
        cost: +this.form.cost,
      }
      this.createRollItem(roll)

      this.form = {
        name: null,
        price: null,
      }
    },
    deleteRollItem(id) {
      this.deleteRoll(id)
    }
  },
}
</script>

